.composite__gridContainer___39rFL{
    display: grid;
    grid-template-columns: 2fr 2fr 2fr 2fr 1fr;
    grid-template-rows: 1fr 3fr 3fr;
    grid-template-areas: 
                        "header header header header buttons"
                        "composite composite composite composite buttons"
                        "english math reading science buttons";
    grid-row-gap: 7px;
    row-gap: 7px;
    grid-column-gap: 7px;
    -webkit-column-gap: 7px;
            column-gap: 7px;
    height: 98vh;
    width: 98vw;
    margin: 1rem;
}

.composite__header___sYHNl{
    height: 15vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: linear-gradient(#102e79,#0d2561);
    grid-area: header;
    color: white;
}

.composite__header___sYHNl > img {
    height: 87%;
    margin: 1rem 4rem 1rem 1rem;
}

.composite__header___sYHNl > h1{
    font-weight: normal;
    margin: 0 0 0 3rem;
    font-size: 6rem;
}

.composite__composite___1zyF1{
    background-color: rgb(230, 230, 230);
    border: 5px solid #102e79;
    grid-area: composite;
}

.composite__buttons___1-ZES{
    background-image: linear-gradient(#102e79,#0d2561);
    grid-area: buttons;
}

.composite__section___1smFa{
    background-color: rgb(230, 230, 230);
    display: flex;
    flex-direction: column;
}

.composite__sectionHeader___1Ld78{
    top: 0;
    background-image: linear-gradient(#102e79,#0d2561);;
    text-align: center;
    flex-grow: 1;
}

.composite__sectionTitle___VJKzq{
    display: inline-block;
    color: white;
    font-size: 4rem;
    width: 100%;
}

.composite__flexContainer___E1PV2{
    display: flex;
    flex-wrap: wrap;
    height: 80%;
    width: 100%;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
}

.composite__button___1TP9x {
    margin: 1rem;
    font-family: 'Open Sans';
    border: 1px rgb(223, 223, 223) solid;
    font-size: 1.3rem;
    font-weight: bold;
    min-height: 4rem;
    width: 8vw;
    border-radius: 1rem;
    background-image: linear-gradient(#FDFDFD,rgb(223,223,223));
    color:rgb(36, 36, 36);
}

.composite__button___1TP9x:hover {
    cursor: pointer;
    background-image:none;
    background-color: #FDFDFD;
    color: rgb(36, 36, 36)
}

.composite__english___3G_vF{
    grid-area: english;
}

.composite__math___3WYI-{
    grid-area: math;
}

.composite__reading___1Ghc6{
    grid-area: reading;
}

.composite__science___1acwk{
    grid-area: science;
}

.composite__selected___1mtQW, .composite__selected___1mtQW:hover {
    background-image: linear-gradient(#102e79,#0d2561);
    color: #FDFDFD;
    border: solid white 2px;
}
