.sectionSummary__gridContainer___SpAcj{
    display: grid;
    grid-template-columns: 2fr 2fr 2fr 2fr 1fr;
    grid-template-rows: 1fr 2fr;
    grid-template-areas: 
                        "composite composite composite composite buttons"
                        "questionTypes general general timeManagement buttons";
    grid-row-gap: 7px;
    row-gap: 7px;
    grid-column-gap: 7px;
    -webkit-column-gap: 7px;
            column-gap: 7px;
    height: 98vh;
    width: 98vw;
    margin: 1rem;
}

.sectionSummary__header___N29Ee{
    height: 15vh;
    display: flex;
